<template>
  <div id="friendlist" class="content">
    <div ref="friendlist" class="friendlist">
      <div ref="search" class="search">
        <search />
      </div>

      <div class="chatlist" :style="{ height: chatlist_height + 'px' }">
        <chatlist />
      </div>
    </div>
    <div ref="chatbox" class="chatbox">
      <div ref="message" class="message" :style="{ height: message_height + 'px' }">
        <message />
      </div>
      <div ref="v_text" class="v_text">
        <v-text />
      </div>
    </div>
  </div>
</template>

<script>
import search from '../../components/search/search'
import chatlist from '../../components/chatlist/chatlist'
import message from '../../components/message/message'
import vText from '../../components/text/text'
import { mapState } from 'vuex'
export default {
  components: {
   	 search,
   	 chatlist,
   	 message,
   	 vText
  },
  data() {
    return {
      message_height: null,
      chatlist_height: null
    }
  },
  computed: {
    ...mapState([
      'top',
      'left',
      'width',
      'height'
    ])
  },
  watch: {
    top: function() {
      this.chatlist_height = this.$refs.friendlist.clientHeight - this.$refs.search.clientHeight
      this.message_height = this.$refs.chatbox.clientHeight - this.$refs.v_text.clientHeight
    }
  },
  created() {
  },
  mounted() {
    console.log('zhahdiu=----=-=-wfknewkj', this.$refs.v_text.clientHeight, this.$refs.chatbox.clientHeight, this.$refs.message.clientHeight)
    this.chatlist_height = this.$refs.friendlist.clientHeight - this.$refs.search.clientHeight
    this.message_height = this.$refs.chatbox.clientHeight - this.$refs.v_text.clientHeight
    // this.$refs.message.style.height = this.$refs.chatbox.clientHeight - this.$refs.v_text.clientHeight
  }
}
</script>

